/*::::::::::::::::::::::::::::::::::::::
Internal Data
::::::::::::::::::::::::::::::::::::::*/
const Data = {
	"Agent": undefined,
	"DeviceType": undefined,
	"Width": undefined,
	"Height": undefined,
	"Orientation": undefined,
	"OperatingSystem": undefined,
	"Language": undefined,
	"Languages": undefined,
	"ColourScheme": undefined,
	"BrandName": undefined,
	"WebApplication": false
};
const QueryList = {
	"Bot": [
		"bot", "generator", "http", ".com", ".net", ".org", ".io", "preview", "crawler", "google page speed insight"
	],
	"Television": [
		"viera", "netcast", "boxee", "kylo", "roku", "dlnadoc", "ce-html"
	]
};

/*::::::::::::::::::::::::::::::::::::::
General Event Handle
::::::::::::::::::::::::::::::::::::::*/
function EventHandle_UnexpectedEvent(Event) {
	console.error("[JavaScript Macro - Snooper] " + Event);
};

/*::::::::::::::::::::::::::::::::::::::
Fetch Data
::::::::::::::::::::::::::::::::::::::*/
try {
	Data["Agent"] = navigator.userAgent;
} catch (Error) {
	EventHandle_UnexpectedEvent(Error);
};
try {
	Data["Width"] = window.innerWidth;
} catch (Error) {
	EventHandle_UnexpectedEvent(Error);
};
try {
	Data["Height"] = window.innerHeight;
} catch (Error) {
	EventHandle_UnexpectedEvent(Error);
};
try {
	Data["Language"] = (navigator.language || navigator.userLanguage || navigator.browserLanguage || navigator.systemLanguage).toLowerCase();
} catch (Error) {
	EventHandle_UnexpectedEvent(Error);
};
try {
	Data["Languages"] = navigator.languages;
} catch (Error) {
	EventHandle_UnexpectedEvent(Error);
};
if (matchMedia("(prefers-color-scheme: light)").matches == true) {
	Data["ColourScheme"] = "Light";
} else if (matchMedia("(prefers-color-scheme: dark)").matches == true) {
	Data["ColourScheme"] = "Dark";
};
if (matchMedia("(display-mode: standalone)").matches == true || navigator.standalone == true) {
	Data["WebApplication"] = true;
};
let Data_Agent_LowerCase = Data["Agent"].toLowerCase();

/*::::::::::::::::::::::::::::::::::::::
Determine Device Type
::::::::::::::::::::::::::::::::::::::*/
for (let index = 0; index < QueryList["Bot"].length; index++) {
	if (Data_Agent_LowerCase.search(QueryList["Bot"][index]) != -1) {
		Data["DeviceType"] = "Bot";
		break;
	};
};
if (Data["DeviceType"] != "Bot") {
	if (Data["Agent"].search("TV") != -1 || function () {
		for (let index = 0; index < QueryList["Television"].length; index++) {
			if (Data_Agent_LowerCase.search(QueryList["Television"][index]) != -1) {
				return true;
			};
		};
		return false;
	} == true) {
		Data["DeviceType"] = "Television";
	} else if (typeof window.process == "object") {
		Data["DeviceType"] = "Node Webkit";
	} else if (Data_Agent_LowerCase.search("mobile") != -1) {
		Data["DeviceType"] = "Mobile";
	} else if (Data_Agent_LowerCase.search("tablet") != -1 || Data_Agent_LowerCase.search("touch") != -1) {
		Data["DeviceType"] = "Tablet";
	} else if (Data_Agent_LowerCase.search("kiosk") != -1) {
		Data["DeviceType"] = "Kiosk";
	} else {
		Data["DeviceType"] = "Desktop";
	};
};

/*::::::::::::::::::::::::::::::::::::::
Determine Brand Name
::::::::::::::::::::::::::::::::::::::*/
if (Data["Agent"].search("Firefox") != -1) {
	Data["BrandName"] = "Mozilla Firefox";
} else if (Data["Agent"].search("Edge") != -1) {
	Data["BrandName"] = "Microsoft Edge";
} else if (Data["Agent"].search("Chrome") != -1) {
	Data["BrandName"] = "Google Chrome";
} else if (Data["Agent"].search("Safari") != -1) {
	Data["BrandName"] = "Apple Safari";
} else {
	Data["BrandName"] = "Unknown";
};

/*::::::::::::::::::::::::::::::::::::::
Determine Operating System
::::::::::::::::::::::::::::::::::::::*/
if (Data["Agent"].search("Windows") != -1) {
	Data["OperatingSystem"] = "Windows";
} else if (Data["Agent"].search("Macintosh") != -1 || Data["Agent"].search("Mac OS") != -1 || Data["Agent"].search("OS X") != -1) {
	Data["OperatingSystem"] = "Macintosh";
} else if (Data["Agent"].search("Android") != -1) {
	Data["OperatingSystem"] = "Android";
} else if (Data["Agent"].search("CrOS") != -1) {
	Data["OperatingSystem"] = "ChromeOS";
} else if (Data["Agent"].search("Linux") != -1) {
	Data["OperatingSystem"] = "Linux";
} else {
	Data["OperatingSystem"] = "Unknown";
};

/*::::::::::::::::::::::::::::::::::::::
Handle Size/Resize
::::::::::::::::::::::::::::::::::::::*/
/* Determine Orientation */
function DetermineOrientation_Internal() {
	if (Data["Width"] >= Data["Height"]) {
		Data["Orientation"] = "Landscape";
	} else {
		Data["Orientation"] = "Portrait";
	};
};
/* On Resize */
window.addEventListener("resize", function () {
	Data["Width"] = window.innerWidth;
	Data["Height"] = window.innerHeight;
	DetermineOrientation_Internal();
});

/*::::::::::::::::::::::::::::::::::::::
Define Constant
::::::::::::::::::::::::::::::::::::::*/
const Agent = Data["Agent"];
const DeviceType = Data["DeviceType"];
const Width = Data["Width"];
const Height = Data["Height"];
const Orientation = Data["Orientation"];
const OperatingSystem = Data["OperatingSystem"];
const OS = OperatingSystem;
const Language = Data["Language"];
const Languages = Data["Languages"];
const ColourScheme = Data["ColourScheme"];
const BrandName = Data["BrandName"];
const WebApplication = Data["WebApplication"];

/*::::::::::::::::::::::::::::::::::::::
Package Module
::::::::::::::::::::::::::::::::::::::*/
export {
	Agent,
	DeviceType,
	Width,
	Height,
	Orientation,
	OperatingSystem,
	OS,
	Language,
	Languages,
	ColourScheme,
	BrandName,
	WebApplication
};
